import React, { useRef, useEffect, useState } from 'react'
import { useHistory } from 'react-router-dom'
import axios from 'axios'
import {
    BrowserRouter as Router,
    Link,
  } from "react-router-dom";
import './Login.scss'


export default function Login(props) {



    // const mystorage = window.localStorage
    // const [currentUsername, setCurrentUsername] = useState('')
    // useEffect(()=> {
    //     var localUserInfo = mystorage.userInfo ? JSON.parse(mystorage.userInfo): ''
    //     setCurrentUsername(localUserInfo.username)
    // },[currentUsername])
    const usernameRef = useRef()
    const passwordRef = useRef()
    // const captchaRef = useRef()
    const history = useHistory()

    useEffect(() => {
        if (props.userInfo) {
            history.push('/home')
        }
    },[])


    async function submit(e) {
        e.preventDefault()
        var data = {
            name: usernameRef.current.value,
            password: passwordRef.current.value,
            // captcha: captchaRef.current.value,
        }
        try {
            var res = await axios.post('/login', data)
            if (res.data.code != -1) {
                // console.log(res.data)
                props.setUserInfo(res.data)
                // props.onLogin && props.onLogin(res.data)
                // mystorage.setItem('userInfo',JSON.stringify(res.data))
                // setCurrentUsername(res.data.currentUsername)
                history.push('/home')
            }
        } catch (e) {
            // console.log(e.response)
        alert(e.response.data.msg.toString())
        }
    }

    return (
        <div>
            <div className="title"><span>登录页面</span></div>
            <h2 className="login_nav">用户登录</h2>
            <form className="login_form" onSubmit={submit}>
                <div className="login_form_item"><div>用户名：</div><div><input className="login_form_input" ref={usernameRef} type="text" /></div></div>
                <div className="login_form_item"><div>密码：</div><div><input className="login_form_input" ref={passwordRef} type="password" /></div></div>
                {/* <img src="http://localhost:8080/captcha" alt="注册码" /> <br/> */}
                {/* Captcha:  <input ref={captchaRef}  type="text" /><br /> */}
                <button className="finish_btn">登录</button>
                <Link to="/register"><button className="finish_btn">前往注册</button></Link>
                <section className="login_form_item login_tip">
                tips: 本demo可用<i>用户名: lily, 密码: 123456</i>或<br/><i>用户名: haha, 密码: 213213</i>直接登录.
                也可以注册尝试
                </section>
            </form>

            {/* <Link to="/forget" >忘记密码?</Link> */}


        </div>
    )
}
