import React, { useEffect, useState } from 'react'
import axios from 'axios'
import { useHistory, Link, useLocation } from 'react-router-dom'
import './My.scss'

export default function My({userInfo ,setUserInfo}) {
    // console.log('My中的用户信息',userInfo)

    var localStorage = window.localStorage
    var history = useHistory()
    var [myVotes, setMyVotes] = useState([])
    // 登出页面
    function logout() {
        axios.get('/logout').then(() => {
            // history.push('/login')
            setUserInfo(null)
        })
        localStorage.clear()
        history.push('/login')
    }

    useEffect(() => {
        async function getVotes() {
            let res = await axios.get('/myvotes')
            setMyVotes(res.data)
        }
        getVotes()
    }, [])

    // 删除投票
    async function deleteMyvote(voteId) {
        var res = await axios.post('/delete-vote', {
            voteId
        })
        setMyVotes(res.data)
    }
    return (

        <div>
            <ul>
                {
                myVotes.map((vote, idx) => {
                    // 判断是否过期
                    let voteDeadline = new Date(vote.deadline)
                    let isOverdue = (voteDeadline < Date.now()) ? true : false
                    // console.log('帖子id：', vote.id, '用户id：', userInfo.id)
                    return <li className="create_vote_list myvote_list" key={idx}>
                        <Link className="vote_link" to={`/vote/${vote.id}`}>
                            <span>{vote.title}</span>
                            { isOverdue &&
                                <span style={{color: 'rgba(230,0,18,0.4)',fontSize: '14px'}}>(已过期)</span>
                            }
                        </Link>
                        { userInfo && vote && (vote.userId === userInfo.id) &&
                            <button className="delete_vote" onClick={() => deleteMyvote(vote.id)}>删除</button>
                            }
                    </li>
                })
                }
            </ul>
            <button className="finish_btn" onClick={logout}>登出</button>
        </div>
    )
}
