import React, { useEffect, useState } from 'react';
import './App.css';
import {
  Redirect,
  Route,
  Switch,
  useHistory
} from 'react-router-dom'
import axios from 'axios'
import Login from './Login.jsx'
import Home from './Home.jsx'
import CreateVote from './CreateVote'
import ViewVote from './ViewVote.jsx'
import Register from './Register.jsx'
import url from './url'

function App() {
  var history = useHistory()
  var [userInfo, setUserInfo] = useState(undefined)
  window.setUserInfo = setUserInfo
  console.log(url)
  useEffect(() => {
    //  进入页面时发送一个验证信息判断是否登录
    axios.get('/userInfo').then(res => {
      window.user = res.data
      console.log('userInfo', res.data)
      setUserInfo(res.data)
    })
  },[])

  // 如果未登录直接跳转到登录界面
  if(!userInfo) {
    history.push('/login')
  }

  return (
      <div className="App">
        <Switch>
          <Route path="/" exact>
            <Redirect to="/home" />
          </Route>
          <Route path="/login">
            <Login setUserInfo={setUserInfo} userInfo={userInfo}/>
          </Route>
          <Route path="/register">
            <Register />
          </Route>
          <Route path="/home">
            <Home userInfo={userInfo} setUserInfo={setUserInfo} />
          </Route>
          <Route path="/create-vote" >
            <CreateVote />
          </Route>
          <Route path="/vote/:id" >
            {userInfo &&
              <ViewVote userInfo={userInfo} />
            }
          </Route>
        </Switch>
      </div>
  );
}

export default App;
