
import React from 'react'
import { useInput, useBoolean, useArray} from 'react-hanger'
import axios from 'axios'
import { useHistory, useLocation } from 'react-router-dom'
import 'antd/dist/antd.css';
import { Switch } from 'antd';
import './CreateVote.scss'
import './iconfont.css'
function useQuery() {
    return new URLSearchParams(useLocation().search);
}
export default function CreateVote () {
    var query = useQuery()
    var history = useHistory()
    var options = useArray(['',''])
    var title  = useInput()
    var desc = useInput()
    var deadline = useInput()
    var anonymous = useBoolean()
    var isMultiple = useBoolean(query.get('multiple') == '1'? true : false)

    // 返回函数
    function goBack() {
        history.goBack()
    }

    // 添加选项
    function getMoreOption() {
        options.push('')
        isMultiple.setFalse()
    }
    function handleDeleteOption(idx) {
        if(options.length == 2) {
            return
        }
        options.removeIndex(idx)
        if(options.length == 2) {
            isMultiple.setTrue()
        }
    }
    async function createVote() {
        try {
            await axios.post('/vote',{
                title: title.value,
                desc: desc.value,
                options: options.value,
                deadline: new Date(deadline.value).toISOString(),
                anonymous: anonymous.value ? 1 : 0,
                isMultiple: isMultiple.value ? 1 : 0,
            })
            alert('创建成功')
            history.push('/home')
        } catch (e) {
            alert('创建失败')
        }
    }
    return (
        <div>
            <div className="title"><span><i onClick={goBack} className="iconfont icon-fanhui"></i></span>&nbsp;创建投票</div>
            <div className="createVote_content">
                <div><input className="vote_title" value={title.value} onChange={title.onChange} type="text" placeholder="投票标题" /></div>
                <div><input className="vote_item" value={desc.value} onChange={desc.onChange} type="text" placeholder="补充描述 [选填]" /></div>
                <ul className="vote_ul">
                    {
                        options.value.map((it, idx) => {
                            return <li className="create_vote_list" key={idx}>
                                <button className="create_vote_btn" onClick={() => handleDeleteOption(idx)}><span>-</span></button>
                                <input className="create_vote_text" type="text" value={it} onChange={(e) => options.setValue([...options.value.slice(0, idx), e.target.value, ...options.value.slice(idx + 1)])}/>
                            </li>
                        })
                    }
                </ul>
                <div className="create_vote_list" ><button className="create_vote_btn vote_add" onClick={getMoreOption}><span>+</span></button><span>添加选项</span></div>
                <div className="create_vote_list create_vote_space">截止日期: <input className="deadline" value={deadline.value} onChange={deadline.onChange} type="datetime-local" /></div>
                <div className="create_vote_list create_vote_select"><div>匿名投票: </div><Switch className="switch_btn" onChange={anonymous.toggle} checked={anonymous.checked}></Switch></div>
                <div className="create_vote_list create_vote_select"><div>多选: </div><Switch className="switch_btn" checked={isMultiple.value} onChange={isMultiple.toggle}></Switch></div>
            </div>
            <div><button className="finish_btn" onClick={createVote}>创建</button></div>
        </div>
    )
}
