import 'antd/dist/antd.css'
import Axios from 'axios'
import React, { useState, useEffect } from 'react'
import { useHistory, useParams } from 'react-router-dom'
import {groupBy} from 'lodash'
import {
    ShareAltOutlined
  } from '@ant-design/icons';
import './ViewVote.scss'
import './iconfont.css'
import url from './url.js'

export default function ViewVote({ userInfo }) {
    // 替换url为wsUrl
    var wsUrl = url.replace(/https?/, 'ws')
    var history = useHistory()
    var { id } = useParams()
    var [loading, setLoading] = useState(true)
    var [voteInfo, setVoteInfo] = useState(null)
    // var [votings, setVotings] = useState(null)

    // 返回按钮
    function goBack() {
        history.goBack()
    }


    if (!loading && voteInfo) {
        var groupedVotings = groupBy(voteInfo.votings, 'optionId')
        var uniqueUsersCount = new Set(voteInfo.votings.map(it => it.userId)).size
    }
    useEffect(() => {
        async function fetchData() {
            setVoteInfo(null)
            setLoading(false)
            await Axios.get(`/vote/${id}`).then((res) => {
                setVoteInfo(res.data)
                // setVotings(res.data.votings)
                setLoading(false)
            })
        }
        fetchData()
    } ,[id])
    useEffect(() => {
        if(!voteInfo) {
            return
        }
        // 用于接收某个vote的新的选票信息的
        if(Date.now() < new Date(voteInfo.deadline).getTime()) {
            var ws = new WebSocket(`${wsUrl}/vote/${id}`)
            ws.onmessage = e => {
                var votings = JSON.parse(e.data)
                setVoteInfo({
                    ...voteInfo,
                    votings
                })
            }
        } else {
            return
        }
        return () => ws.close()
    }, [voteInfo])

    // 点击投票系统
    async function voteUp(optionId, hasVoted) {
        // 已过截止日期
        if(Date.now() > new Date(voteInfo.deadline).getTime()) {
            return
        }
        // if (!hasVoted) {
        //     var thisVoting = {
        //         id: -1,
        //         optionId: optionId,
        //         voteId: id,
        //         userId: userInfo.id,
        //         avatar: userInfo.avatar,
        //     }
        //     setVotings([...votings,thisVoting])
        // } else {
        //     var filterVotings = votings.filter(it => {
        //         return !(it.userId == userInfo.id && optionId == it.optionId)
        //     })
        //     setVotings(filterVotings)
        // }
        var res = await Axios.post(`/voteup/${id}`,{
            optionId,
            isVoteDown: hasVoted,
        })
        // setVoteInfo(res.data)
    }

    // 计算进度条
    function calPCT(divisor, dividend) {
        if(!dividend) {
            return 0
        } else {
            return (100 * divisor / dividend).toFixed(2)
        }
    }



    if(!voteInfo) {
        return <div>loading...</div>
    } else {
        return (
            <div >
                <div className="title"><i onClick={goBack} className="iconfont icon-fanhui"></i>&nbsp;投票结果</div>
                <div className="voteInfo_title"><h2>{voteInfo.title}</h2><div><ShareAltOutlined /></div>
                </div>
                <p className="voteInfo_desc">{voteInfo.desc? voteInfo.desc: '空'}<span>{voteInfo.isMultiple ? '[多选]' : '[单选]'}</span></p>
                <ul>
                    {
                        voteInfo.options.map((option) => {
                            var userId = option.userId // 这个选项是哪个用户投的
                            var currVotings = groupedVotings[option.id] ? groupedVotings[option.id] : [] //当前选项的所有投票
                            var hasCurrentVote = currVotings.find(it => it.userId === userInfo.id) ? true : false
                            return <li className="voteInfo_content" key={option.id} onClick={() => voteUp(option.id, hasCurrentVote)}>
                                {/* <input type="checkbox" className="checkbox_item" onChange={() => voteUp(option.id, hasCurrentVote)} checked={hasCurrentVote} /> */}
                                <span className="option-text">{option.content}</span>
                                {' '}
                                { hasCurrentVote &&
                                    <i className="iconfont icon-gou"></i>}

                                <strong> {currVotings.length} 票 {'  '} {calPCT(currVotings.length, uniqueUsersCount)}%</strong>
                                <div className="option-ratio" style={{width: calPCT(currVotings.length, uniqueUsersCount)+'%'}} ></div>
                                <ul className="avatars">
                                    {
                                        currVotings.map((voting, idx) => {
                                            return <li key={idx}><img className="avatar" src={voting.avatar}></img></li>
                                        })
                                    }
                                </ul>
                            </li>
                        })
                    }
                </ul>
                <p className="deadline_tip">投票截至：{new Date(voteInfo.deadline).toLocaleString()}</p>
            </div>
        )
    }
}
