import React  from 'react'
import { Redirect, Route, useRouteMatch ,NavLink } from 'react-router-dom'
import Create from './Create.jsx'
import My from './My'
import './Home.scss'
import './iconfont.css'

export default function Home({userInfo, setUserInfo}) {
    let { path, url} = useRouteMatch()
    // console.log('用户信息：',userInfo)
    return (
        <div>
            <div className="title">VoteDemo</div>
            <Route path={`${url}/`} exact >
                <Redirect to={`${url}/create`} />
            </Route>

            <Route path={`${url}/create`} >
                <Create />
            </Route>
            <Route path={`${url}/my`} >
                <My userInfo={userInfo} setUserInfo={setUserInfo} />
            </Route>
            <div className="navbar">
                <div className="navbar_item">
                    <div>
                        <NavLink activeClassName="active" to={`${url}/create`}><i className="iconfont icon-xinjian1"></i><br />新建</NavLink>
                    </div>
                </div>
                <div className="navbar_item">
                    <div>
                        <NavLink to={`${url}/my`}><i className="iconfont icon-wodedangxuan"></i><br />我的</NavLink>
                    </div>

                </div>
            </div>

        </div>
    )
}
