import React from 'react'
import { Link } from 'react-router-dom'
import "./Create.scss"
// import url from './url.js'
// import p1 from '../xx/yy/aa.png'

export default function Create() {
    return (
        <div className="create_content">
            <div className="create_item">
                    <img src={`/static/vote1.png`} alt="vote1"/>

                    <Link to="/create-vote">
                        <div className="create_item_btn">
                        创建单选
                        </div>
                    </Link>
            </div>
            <div className="create_item">
                    <img src={`/static/vote2.png`} alt="vote2"/>
                    <Link to="/create-vote?multiple=1">
                        <div className="create_item_btn">
                        创建多选
                        </div>
                    </Link>
            </div>
        </div>
    )
}
